
:root{
  --blackPieceColor: #000;
  --whitePieceColor: #fff;
  --ValidMoveColor: #a0f7a0;
  --SelectedPieceColor: yellow;
  --darkSquareColor: #784F48;
  --lightSquareColor: #f0d9b5;
}
/*utilities styles*/
nav{
  min-height: 30px;
  padding: 10px;
  background-color: #fff;
  margin-bottom: 10px;
}

.game-menu{
  font-weight: 600;
  font-size: 1.1rem;
  text-transform: capitalize;
  display: flex;
   flex-direction: column;
  gap: 20px;
  justify-content: space-between;
  font-family: 'Courier New', Courier, monospace;
}
.game-menu span{
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
  color: #f0d9b5;
  text-transform: capitalize;
  background-color: #784F48;
  padding: 7px;
  border-radius: 8px;

}
.game-menu-items{
  display: flex;
  gap: 10px;
  align-items: center;
  flex-wrap: wrap;
}
 button{
  border: none;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
  color: #f0d9b5;
  cursor: pointer;
  text-transform: capitalize;
  background-color: #784F48;
  min-height: 25px;
  padding: 7px;
  border-radius: 8px;
  min-width: 70px;
  flex: 1;
}
.steak{
  border: none;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
  position: absolute;
  top: calc(50% - 30px / 2);
  left: calc(50% - 120px / 2);
  animation-name: fadeOut;
  animation-duration: 3s;
  color: #000000;
  text-transform: capitalize;
  background-color: rgb(255, 217, 0);
  min-height: 25px;
  width: 120px;
  padding: 7px;
   opacity: 0;
  border-radius: 8px;
}
.game-over{
  font-family: 'Courier New', Courier, monospace;
  font-weight: 600;
  position: absolute;
  width: 30%;
  min-height: 160px;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  background-color: #f0d9b5;
  color: 000;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
  padding: 5px;
  border-radius: 8px;
  z-index: 999;
}
.overlayer{
  width: 100%;
  height: 100%;
  background-color: #00000070;
  position: absolute;
  inset: 0;
  z-index: 1;
  display: flex;
}
.game-over span{
  text-transform: capitalize;

}
.game-over button{
  flex: 0;
  width: 80%;
}
.steak-container{
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 1;
}
/* board styles */
.board {
    display: grid;
    grid-template-rows: repeat(8, 50px);
    position: relative;
     box-shadow: 25px 25px 10px 0px rgba(0, 0, 0, 0.346);
  }
  .board::after{
    content: '';
    background-color: var(--darkSquareColor);
    position: absolute;
    transform: skewX(45deg);
    right: -15px;
		height: 100%;
		width: 10px;
		bottom: -8px;
		transform: skewY(45deg);
    border-right: 5px solid var(--lightSquareColor);
  }
  .board::before{
    content: '';
    background-color: var(--darkSquareColor);
    position: absolute;
    transform: skewX(45deg);
    bottom: -15px;
		height: 10px;
		width: 100%;
 
		left: 8px;
    border-bottom: 5px solid var(--lightSquareColor);
  }
  .row {
    display: flex;
  }
  
  .square {
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  
  .dark {
    background-color: var(--darkSquareColor);
  }
  
  .light {
    background-color: var(--lightSquareColor);
  }
  
  .piece {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-size: 70%;
    background-repeat: no-repeat;
    background-position: center center;
    image-rendering: pixelated;
  }
  
  .piece.b {

  background-color: var(--blackPieceColor);
 
  }
  .piece.w{
  background-color: var(--whitePieceColor);

  }
  .piece.bk{

    background-color: var(--blackPieceColor);
    fill: #fff;
    background-image: url('./assets/whiteCrown.svg');
    
  }
  .piece.wk{

    background-color: var(--whitePieceColor);
    background-image: url('./assets/blackCrown.svg');
    fill: #000;

  }

  .square.valid-move {
    background-color: var(--ValidMoveColor); 
  }
  
  .square.selected-piece {
   background-color: var(--SelectedPieceColor);
  }
  @keyframes fadeOut {
    0%{
     opacity: 1;
    }
    100%{
     opacity: 0;
    }
  }
  @keyframes flipToKing {
    from { -webkit-transform: rotateY(0); -moz-transform: rotateY(0); transform: rotateY(0); }
    to { -webkit-transform: rotateY(1800deg); -moz-transform: rotateY(1800deg); transform: rotateY(1800deg);
     }
    }