.game{
    width: 100%;
    min-height: 100vh;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    font-family: 'Courier New', Courier, monospace;
}
.container{
    width: auto;
    height: 60%;
}
.inner-container{
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.history-container{
    overflow-y: scroll;
    height: 50px;
    font-size: 0.7rem;
}
.history-container ul{
    margin: 0;
    padding: 0;
}